.certifications-wrapper {
  margin-top: -4px;
  display: grid;
  grid-template-columns: [left-s] 1fr [left-e right-s] 1fr [right-e] 5vw;
  grid-template-rows: [title-s] 200px [title-e row1-s] 200px [row1-e row2-s] 275px [row2-e];
  color: black;
  background: rgb(255,255,255);
  background: linear-gradient(0deg, rgba(255,255,255,1) 0%, rgba(0,0,0,1) 100%);
  z-index: 1;

  .certifications-title {
    grid-column: left-s/left-e;
    grid-row: title-s/title-e;
    display: flex;
    margin: 2vh 0vw 0vh 2vw;

    .blue-circle {
      height: 200px;
      width: 200px;
      border-radius: 100px;
      background-color: #01a1ff;
    }

    .full-stack-title {
      font-size: 64px;
      transform: translate(75px, 75px);
      font-weight: 700;
      position: absolute;
      color:white;
      text-shadow: 2px 2px 2px #000000;
      // background: #000000;
      // background: linear-gradient(to top, #000000 25%, #FFFFFF 100%);
      // background-clip: border-box;
      // -webkit-background-clip: text;
      // -webkit-text-fill-color: transparent;
    }

    .full-stack-title-animation {
      animation: fullStackTitleSlideFromLeft 1s 1;
    }
  }

  &__left {
    grid-column: left-s/left-e;
    grid-row: row1-s/row1-e;

    .stack-list {
      grid-column: left-s/left-e;
      grid-row: row1-s/row2-e;
      margin-left: 5vw;
      font-size: 24px;
      text-align: left;
    }

    li {
      margin-bottom: 1vh;
      animation: listItemsSlideFromLeft .3s 1;
      color: white;
      text-shadow: 2px 2px 2px #000000;
    }
  }

  &__right {
    position: relative;
    grid-column: right-s/right-e;
    grid-row: row1-s/row1-e;
    grid-auto-flow: row;
    display: grid;
    grid-template-rows: [row1-s] 150px [row1-e row2-s] 300px [row2-e];
    align-items: center;
    width: 50vw;

    .about-certifications {
      grid-row: row1-s/row1-e;
      align-self: flex-start;
      text-indent: end;
    }

    .binary-video {
      position: absolute;
      grid-row: row2-s/row2-e;
      object-fit: cover;
      position: relative;
      justify-self: center;
      width: 400px;
      border-radius: 50px;
      background-color: #FFFFFF;
      border: 15px solid #333333;
      z-index: 0;
    }

    .icons-wrapper {
      position: absolute;
      grid-row: row2-s/row2-e;
      align-self: center;
      justify-self: center;
      max-height: 400px;
      max-width: 400px;
      z-index: 1;

      .icon {
        margin: 20px 20px 20px 20px;
        height: 40px;
        width: 40px;
      }
    }
  }
}

