.projects-wrapper {
  display: flex;
  overflow: hidden;
  width: 100%;

  &__left {
    display: flex;
    width: 25vw;
    align-items: center;
    justify-content: center;
    background-color: black;
    color: white;
    border-bottom-right-radius: 500px;

    .multi-talent {
      font-size: 48px;
      text-justify: center;
      margin: 0vh 5vw;
    }
  }

  &__right {
    display: flex;
    flex-direction: column;
    width: 75vw;
    background-color: white;
    color: black;

    &__blue-circle {
      height: 200px;
      width: 200px;
      border-radius: 100px;
      background-color: #01a1ff;
      position: absolute;
      transform: translateX(-90px);
      z-index: 0;
    }

    .projects-header {
      text-align: left;
      font-size: 64px;
      position: relative;
    }

    .projects-wrapper-inner {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin: 5vh 5vw;
      overflow: hidden;
      width: 750px;

      #ReactSimpleImageViewer {
        z-index: 10;
      }

      .projects-about {
        display: flex;
        margin: 5vh;
        text-align: left;
      }

      .projects-photo-wrapper {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        justify-content: space-around;
        align-items: center;
        align-content: center;
        height: 540px;
        width: 300px;
      }
    }

    .building-header {
      font-size: 64px;
    }
  }
}
